<template>
  <div class="vueStar-wrapper">
    <div class="vueStar__ground">
      <div class="vueStar__icon" @click="toggle" :class="AnimateClass" :style='{color:ColorValue}'>
        <slot name="icon"></slot>
      </div>
      <div class="vueStar__decoration" :class="{ 'vueStar__decoration--active':active}"></div>
    </div>
  </div>
</template>

<script>
  import {isColors} from '@/common/js/common'

  export default {
    name: 'vueStar',
    props: {
      animate: String,
      color: String
    },
    methods: {
      toggle() {
        this.active = !this.active
        this.toggleAnimate = !this.toggleAnimate
        this.toggleColor = !this.toggleColor
      }
    },
    data() {
      return {
        active: false,
        toggleAnimate: false,
        toggleColor: false
      }
    },
    computed: {
      AnimateClass() {
        return this.toggleAnimate ? this.animate : ''
      },
      ColorValue() {
        return this.toggleColor ? this.color : ''
      }
    },
    mounted() {
      if (this.color) {
        if (isColors(this.color)) {
          return
        } else {
          console.error('this color must be hexcolor or rgbcolor  ---vueStar')
        }
      } else {
        return
      }
    }
  }
</script>

<style lang="stylus">
  @import "./CoreBase64.styl"

  .vueStar-wrapper
    position: absolute
    .vueStar__ground
      width: 100px
      height: 100px
      position: relative
      display: flex
      align-items: center
      justify-content: center
      .vueStar__icon
        z-index: 888
        .iconfont
          font-size: 40px
      .vueStar__decoration
        width: 100px
        height: 100px
        position: absolute
        left: 0
        top: 0
        bottom: 0
        right: 0
        base64()
        background-position: 0 0
        transition: background-position 1s steps(25)
        transition-duration: 0s
        &--active
          transition-duration: 1s
          background-position: -2500px 0
</style>



