/*
 * 有关数值的一些函数
*/
function aboutArr() {
  let arr = [1, 5, 6, 3];    // 数字数组
  arr.sort(function (a, b) {
    return a - b;   // 从小到大排
    return b - a;   // 从大到小排
    return Math.random() - 0.5;   // 数组洗牌
  });
  console.log(arr)
  arr = [{   // 对象数组
    num: 1,
    text: 'num1'
  }, {
    num: 5,
    text: 'num2'
  }, {
    num: 6,
    text: 'num3'
  }, {
    num: 3,
    text: 'num4'
  }];
  arr.sort(function (a, b) {
    return a.num - b.num;   // 从小到大排
    return b.num - a.num;   // 从大到小排
    return Math.random() - 0.5;   // 数组洗牌
  });
  console.log(arr)
}

/* 关于localStorage的相关操作
 ** key 键值 type为操作类型 value为值
 *  0 代表存入
 *  1 代表获取
 *  2 代表删除
 *  3 代表清空
 */
export function selfLocalStorage(key, type, value) {
  switch (type) {
    case 0:
      localStorage.setItem(key, JSON.stringify(value))
      break
    case 1:
      return JSON.parse(localStorage.getItem(key))
    case 2:
      localStorage.removeItem(key)
      break
    case 3:
      localStorage.clear()
      break
  }
}

/*
 * 设置cookie
 ** key 键值 type为操作类型 value为值 exdays为过期时间 删除cookie传入-1
 * 0为写入cookie
 * 1为获取cookie
 * 2为删除cookie
 * 3为清空cookie
*/
export function selfCookie(key, type, value, exdays) {
  switch (type) {
    case 0:
    case 2:
      let d = new Date();
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
      let expires = 'expires=' + d.toGMTString();
      document.cookie = key + '=' + value + '; ' + expires;
      break
    case 1:
      let name = key + '=';
      let ca = document.cookie.split(';');
      let re = ''
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i].trim();
        if (c.indexOf(name) === 0) {
          re = c.substring(name.length, c.length);
          break
        }
      }
      return re
    case 3:
      let keys = document.cookie.match(/[^ =;]+(?=\=)/g)
      if (keys) {
        for (let i = keys.length; i--;) {
          document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
        }
      }
      break
  }
}

/*
 * 字符串与html实体之间的转换
 ** str 传入的字符串或者html实体 type为操作类型
 * 0为string to html vue里面使用 v-html可以加载入页面
 * 1为 html to string 待考究
*/
export function stringHtmlSwitch(str, type) {
  let re = ''
  switch (type) {
    case 0:
      if (str.length !== 0) {
        re = str.replace(/&amp;/g, '&')
        re = re.replace(/&lt;/g, '<')
        re = re.replace(/&gt;/g, '>')
        re = re.replace(/&nbsp;/g, ' ')
        re = re.replace(/&#39;/g, '\'')
        re = re.replace(/&quot;/g, '\'')
      }
      return re
    case 1:
      re = str.replace(/&/g, '&amp;')
      re = re.replace(/</g, '&lt;')
      re = re.replace(/>/g, '&gt;')
      re = re.replace(/'/g, '&acute;')
      re = re.replace(/'/g, '&quot;')
      re = re.replace(/\|/g, '&brvbar;')
      return re
  }
}

/*
 * 获取当前是pc端还是手机端 返回true代表是pc端 false为手机端
*/
export function pcOrPhone() {
  let userAgentInfo = navigator.userAgent;
  let Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
  let flag = true;
  for (let v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false;
      break;
    }
  }
  return flag;
}

/*
 * 判断是否是颜色
*/
export function isColors(color) {
  const hexColors = /^#([0-9a-fA-F]{6}|[0-9a-fA-F]{3})$/
  const rgbColors = /^[rR][gG][Bb][Aa]?[\(]([\s]*(2[0-4][0-9]|25[0-5]|[01]?[0-9][0-9]?),){2}[\s]*(2[0-4][0-9]|25[0-5]|[01]?[0-9][0-9]?),?[\s]*(0\.\d{1,2}|1|0)?[\)]{1}$/g
  return hexColors.test(color) || rgbColors.test(color)
}

/*
 * 添加dom元素css兼容性前缀
 * dom 操作的元素
 * cssFlag -webkit-transition
 * cssResult all 0.3
*/
export function addWebkitMozOMs(dom, cssFlag, cssResult) {
  let arr = ['-moz-', '-ms-', '-o-', '-webkit-', '']
  arr.forEach(item => {
    dom.style[`${item}${cssFlag}`] = cssResult
  })
}

/*
 * jsonp跨域
 * config{
 *    url:'', // 地址
 *    success:'', // 成功回调
 *    fail:'', // 失败回调
 *    time:1000 // 超时处理
 * }
*/
export function jsonp(config) {
  let options = config || {};   // 需要配置url, success, time, fail四个属性
  let callbackName = ('jsonp_' + Math.random()).replace(".", "");
  let oHead = document.getElementsByTagName('head')[0];
  let oScript = document.createElement('script');
  oHead.appendChild(oScript);
  window[callbackName] = function (json) {  // 创建jsonp回调函数
    oHead.removeChild(oScript);
    clearTimeout(oScript.timer);
    window[callbackName] = null;
    options.success && options.success(json);   // 先删除script标签，实际上执行的是success函数
  };
  oScript.src = options.url + '?' + callbackName;    // 发送请求
  if (options.time) {  // 设置超时处理
    oScript.timer = setTimeout(function () {
      window[callbackName] = null;
      oHead.removeChild(oScript);
      options.fail && options.fail({message: "超时"});
    }, options.time);
  }
}

/*
 * 获取url的参数
 * url 链接地址
*/
export function getURLParam(url) {
  return decodeURIComponent((new RegExp('[?|&]' + url + '=' + '([^&;]+?)(&|#|;|$)', "ig").exec(location.search) || [, ""])[1].replace(/\+/g, '%20')) || null;
}

/*
 * 数组去重
*/
export function arrUnique(arr) {
  let result = [];
  let json = {};
  for (let i = 0, len = arr.length; i < len; i++) {
    if (!json[arr[i]]) {
      json[arr[i]] = 1;
      result.push(arr[i]);  // 返回没被删除的元素
    }
  }
  return result;
}

/*
 * 判断数组是否有重复元素
*/
export function isRepeat(arr) {  //arr是否有重复元素
  let hash = {};
  for (let i in arr) {
    if (hash[arr[i]]) return true;
    hash[arr[i]] = true;
  }
  return false;
}

/*
 * 生成随机数
 * min 最小的数值
 * max 最大的数值
*/
export function randombetween(min, max) {
  return min + (Math.random() * (max - min));
}

/*
 * 判断是否是数组
*/
export function isArray(o) {
  return toString.apply(o) === '[object Array]';
}

/*
 * 判断是否是对象
*/
export function isObject(o) {
  return toString.apply(o) === '[object Object]';
}

// 判断是否为函数
export function isFunction(fn) {
  return Object.prototype.toString.call(fn) === '[object Function]';
};

export function removeItem(removeIds, originIds) {
  for (let i = 0; i < originIds.length; i) {
    if (removeIds.indexOf(originIds[i].id) > -1) {
      originIds.splice(i, 1)
    } else {
      i++
    }
  }
  return originIds
}

/*
 * 格式化时间 2012-12-12
 * time 时间銼
*/
export function forMatTime(time, flag = false) {
  time = new Date(time)
  let year = time.getFullYear()
  let month = time.getMonth() + 1
  month = month > 9 ? month : '0' + month
  let day = time.getDate()
  day = day > 9 ? day : '0' + day
  if (flag) {
    let hour = time.getHours()
    let minute = time.getMinutes()
    minute = minute > 9 ? minute : '0' + minute
    let second = time.getSeconds()
    second = second > 9 ? second : '0' + second
    return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
  }
  return year + '-' + month + '-' + day
}

// 动态加载js
export function loadScript(url, callback) {
  let script = document.createElement('script')
  script.type = 'text/javascript'
  if (script.readyState) { //IE
    script.onreadystatechange = function () {
      if (script.readyState == 'loaded' || script.readyState == 'complete') {
        script.onreadystatechange = null
        callback()
      }
    }
  } else { //Others
    script.onload = function () {
      callback()
    }
  }
  script.src = url
  document.getElementsByTagName('body')[0].appendChild(script)
}



