<template>
  <div class="single-page-scroll-wrapper">
    <swiper :options="swiperOption" class="swiper-box">
      <swiper-slide class="swiper-item" v-for="(item,index) in swipeSlides" :key="index">{{item}}</swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          slidesPerView: 1,
          spaceBetween: 0,
          mousewheel: true,
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          on: {
            click() {
              console.log(this.clickedIndex)
            }
          }
        },
        swipeSlides: ['Slide 1', 'Slide 2', 'Slide 3', 'Slide 4', 'Slide 5', 'Slide 6', 'Slide 7', 'Slide 8', 'Slide 9', 'Slide 10']
      }
    }
  }
</script>

<style lang='stylus'>
  .single-page-scroll-wrapper
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
    .swiper-box
      width: 100%
      height: 100%
      margin: 0 auto
      .swiper-item
        height: 100% !important
        text-align: center
        font-size: 18px
        background: #fff
        /* Center slide text vertically */
        display: -webkit-box
        display: -ms-flexbox
        display: -webkit-flex
        display: flex
        -webkit-box-pack: center
        -ms-flex-pack: center
        -webkit-justify-content: center
        justify-content: center
        -webkit-box-align: center
        -ms-flex-align: center
        -webkit-align-items: center
        align-items: center
</style>



