<template>
  <div class="caver-wrapper">
    <div class="nav-box">
      <swiper :options="swiperOptionFree" ref="swiperOptionFree">
        <swiper-slide v-for="i in navArr" :class="{'active-nav':options.clickedIndex === (i-1)}" :key="i">板块{{i}}
        </swiper-slide>
      </swiper>
    </div>
    <div class="detail-box">
      <swiper :options="swiperOption" ref="swiperOption" class="swiper-box">
        <swiper-slide v-for="(item,index) in list" :style="{left:index*100+'%'}" :key="index">
          <v-loadMore :data="item" :activeIndex="options.clickedIndex"></v-loadMore>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  import loadMore from './loadMore'

  export default {
    data() {
      let options = {
        clickedIndex: 0 // 当前选择的nav栏目
      }
      let activeOptons = {
        activeIndex: 0
      }
      return {
        options: options,
        activeOptons: activeOptons,
        navArr: [1, 2, 3, 4, 5, 6], // 栏目列表
        swiperOptionFree: {
          watchSlidesProgress: true,
          watchSlidesVisibility: true,
          slidesPerView: 3, // 一页显示多少个栏目
          on: {
            tap() {
              options.clickedIndex = this.clickedIndex
            }
          }
        },
        swiperOption: {
          longSwipesRatio: 0.1,
          on: {
            slideChange() {
              activeOptons.activeIndex = this.activeIndex
            }
          }
        },
        list: [
          {url: '', index: 0, loading: false, allLoaded: false},
          {url: '', index: 1, loading: true, allLoaded: false},
          {url: '', index: 2, loading: true, allLoaded: false},
          {url: '', index: 3, loading: true, allLoaded: false},
          {url: '', index: 4, loading: true, allLoaded: false},
          {url: '', index: 5, loading: true, allLoaded: false},
        ],
      }
    },
    computed: {},
    components: {
      'v-loadMore': loadMore
    },
    methods: {},
    created() {
    },
    watch: {
      options: {
        deep: true,
        handler(val) {
          let swiper = this.$refs.swiperOption.swiper
          if (val.clickedIndex !== swiper.activeIndex) {
            swiper.slideTo(val.clickedIndex, 300, false)
          }
        }
      },
      activeOptons: {
        deep: true,
        handler(val) {
          let ix = val.activeIndex
          if (ix !== this.options.clickedIndex) {
            let swiper = this.$refs.swiperOptionFree.swiper
            let min = swiper.activeIndex
            let view = this.swiperOptionFree.slidesPerView
            let max = min + view - 1
            this.options.clickedIndex = ix
            let page = 'false'
            if (ix > max) {
              page = ix - view + 1
            }
            if (ix < min) {
              page = ix
            }
            page !== 'false' && swiper.slideTo(page)
          }
        }
      }
    }
  }
</script>

<style lang='stylus'>
  .caver-wrapper
    position: relative
    .nav-box
      position: fixed
      top: 40px
      left: 0
      width: 100%
      height: 42px
      z-index: 100
      .swiper-slide
        line-height: 3 !important;
        color: #666 !important;
        font-size: 14px !important;
        background: #eee !important;
        text-align: center
        &.active-nav
          background-color: #ddd !important
    .detail-box
      position: fixed
      top: 82px
      left: 0
      bottom: 1.1rem
      width: 100%
      .swiper-box
        height: 100%
        .swiper-wrapper
          height: 100%
          .swiper-slide
            position: absolute
            top: 0
            left: 0
            height: 100%
            width: 100%
            overflow-y: auto
</style>



