import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import home from '@/components/home/home'
import login from '@/components/login/login'

// 学习相关的内容
import study from '@/components/study/study'
import nav from '@/components/study/nav/nav'
import tabContainer from '@/components/study/tabContainer/caver'
import singlePageScroll from '@/components/study/singlePageScroll/singlePageScroll'
import vueStar from '@/components/study/vueStar/star'

Vue.use(Router);

let router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/index',
      component: index,
      redirect: "/home",
      children: [
        {
          path: '/home',
          component: home,
          meta: {
            keepAlive: true // 缓存页面
          }
        },
        {
          path: '/study',
          component: study,
          redirect: "/study/nav",
          children: [
            {
              path: 'nav',
              component: nav,
              meta: {
                keepAlive: true // 缓存页面
              }
            },
            {
              path: 'tabContainer',
              component: tabContainer,
              meta: {
                keepAlive: true // 缓存页面
              }
            },
            {
              path: 'singlePageScroll',
              component: singlePageScroll,
              meta: {
                keepAlive: true // 缓存页面
              }
            },
            {
              path: 'vueStar',
              component: vueStar,
              meta: {
                keepAlive: true // 缓存页面
              }
            },
          ]
        },
      ]
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '*', redirect: "/index"
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    // return new Promise((resolve, reject) => {
    //   setTimeout(() => {
    //     if (savedPosition) {
    //       return savedPosition
    //     } else {
    //       return {x: 0, y: 0}
    //     }
    //   }, 500)
    // })
  }
})
router.beforeEach((to, from, next) => {
  next()
})
export default router



