<template>
  <div class="nav-wrapper">
    <router-link :to="item.url" class="item" v-for="(item,index) in navArr" :key="index">
      <div class="inner">
        <div class="content"><p class="title">{{item.title}}</p></div>
      </div>
    </router-link>
  </div>
</template>

<script type='text/ecmascript-6'>
  let baseUrl = '/study'
  export default {
    data() {
      return {
        navArr: [
          {
            title: '多列滑动', url: `${baseUrl}/tabContainer`
          },
          {
            title: '单屏滑动', url: `${baseUrl}/singlePageScroll`
          },
          {
            title: '星星', url: `${baseUrl}/vueStar`
          },
        ]
      }
    },
    computed: {},
    methods: {},
    created() {
    },
    mounted() {
    },
    watch: {}
  }
</script>

<style lang='stylus'>
  @import "~@/common/stylus/mixins.styl"
  @import "nav.styl"
</style>



