<template>
  <div class="home-wrapper">
    <div class="banner-box">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item,index) in bannerArr" :key="index">
          <router-link to="">
            <img :src="item.src" alt="">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="cart-box" v-infinite-scroll="loadMore"
         infinite-scroll-disabled="loading"
         infinite-scroll-distance="10">
      <router-link to="" class="item border-1px" v-for="(item,index) in list" :key="index">
        <div class="word">
          <h4 class="title">{{item.title}}</h4>
          <div class="abstract">{{item.abstract}}</div>
          <div class="item_info">
            <span class="cmt">观看{{item.view}}</span>
          </div>
        </div>
        <div class="image">
          <img v-lazy="item.src" alt="">
        </div>
      </router-link>
    </div>
    <v-loading :loading="loading" :allLoaded="allLoaded"/>
  </div>
</template>

<script type='text/ecmascript-6'>
  import loading from '@/components/loading/loading'

  export default {
    data() {
      return {
        // 幻灯片数据
        bannerArr: [
          {src: require('./activity_item_1.jpg')},
          {src: require('./activity_item_2.jpg')},
        ],
        allLoaded: false, // 是否全部加载完成
        loading: false, // 是否正在加载数据
        // 数据列表
        list: [
          {
            title: '求这个图原图的地址_百度知道',
            abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
            view: 10 + Math.ceil(Math.random() * 10000),
            src: require('./activity_item_1.jpg')
          },
          {
            title: '求这个图原图的地址_百度知道',
            abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
            view: 10 + Math.ceil(Math.random() * 10000),
            src: require('./activity_item_1.jpg')
          }
        ],
        page: 1
      }
    },
    computed: {},
    components: {
      'v-loading': loading
    },
    methods: {
      loadMore() {
        this.loading = true;
        let data = [{
          title: '求这个图原图的地址_百度知道',
          abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
          view: 10 + Math.ceil(Math.random() * 10000),
          src: require('./activity_item_1.jpg')
        }, {
          title: '求这个图原图的地址_百度知道',
          abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
          view: 10 + Math.ceil(Math.random() * 10000),
          src: require('./activity_item_1.jpg')
        }]
        setTimeout(() => {
          this.page++
          this.list.push(...data)
          if (this.page === 3) {
            this.allLoaded = true
            return false
          }
          this.loading = false;
        }, 3000);
      }
    },
    created() {
    },
    mounted() {
    },
    watch: {}
  }
</script>

<style lang='stylus'>
  @import "~@/common/stylus/mixins.styl"
  @import "./home.styl"
</style>



