<template>
  <div class="index-wrapper">
    <mt-header class="heade-box" fixed title="标题过长会隐藏后面的内容啊哈哈哈哈">
      <mt-button @click="$router.back()" icon="back" slot="left">返回</mt-button>
      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <div class="blank" style="height:40px"></div>
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <div class="bottom-blank-box"></div>
    <v-bottom/>
  </div>
</template>

<script type='text/ecmascript-6'>
  import bottom from '@/components/bottom/bottom'

  export default {
    data() {
      return {}
    },
    computed: {},
    components: {
      'v-bottom': bottom
    },
    methods: {},
    created() {
    },
    mounted() {
    },
    watch: {}
  }
</script>

<style lang='stylus'>
  .index-wrapper
    .bottom-blank-box
      height: 1.1rem
      min-height: 50px
    .heade-box
      z-index: 1000
</style>



