<template>
  <div class="login-wrapper">
    <!--<div class="logo"></div>-->
    <div class="login-content">
      <div class="type-btn">
        <div class="item" :class="{active:loginType===0}" @click="switchType">
          <span>账号登录</span>
          <i></i>
        </div>
        <div class="item" :class="{active:loginType===1}" @click="switchType">
          <span>手机登录</span>
          <i></i>
        </div>
      </div>
      <div class="detail">
        <!--账号密码登录-->
        <div class="input-item border-1px" v-show="loginType===0">
          <i class="iconfont digicon-my"></i>
          <input type="text" v-model="username" placeholder="用户名/手机号">
          <div class="del-btn">
            <i class="iconfont del digicon-roundclosefill" v-show="username" @click="username = ''"></i>
          </div>
        </div>
        <div class="input-item border-1px" v-show="loginType===0">
          <i class="iconfont digicon-lock"></i>
          <input type="password" v-model="pass" v-if="passType" placeholder="密码">
          <input type="text" v-model="pass" v-else placeholder="密码">
          <div class="del-btn">
            <i class="iconfont del" :class="passType ? 'digicon-attention' : 'digicon-attentionforbid'"
               @click="passType = !passType" v-show="pass"></i>
          </div>
        </div>
        <!--手机号验证码登录-->
        <div class="input-item border-1px" v-show="loginType===1">
          <i class="iconfont digicon-mobile"></i>
          <input type="number" v-model="mobile" placeholder="输入绑定的手机号">
          <div class="del-btn">
            <i class="iconfont del digicon-roundclosefill" v-show="mobile" @click="mobile = ''"></i>
          </div>
        </div>
        <div class="input-item border-1px" v-show="loginType===1">
          <i class="iconfont digicon-mark"></i>
          <input type="number" v-model="verifyCode" class="message" placeholder="输入验证码">
          <div class="send-message" @click="getVerifyCode">
            <i class="iconfont del digicon-roundclosefill" v-show="verifyCode" @click="verifyCode = ''"></i>
            <div class="word">{{isSendVerfyCode ? `${countTime}秒后重新发送` : '发送验证码'}}</div>
          </div>
        </div>
        <!--验证码填写-->
        <div class="input-item border-1px" v-show="captchasrc">
          <i class="glyphicon glyphicon-lock"></i>
          <input type="text" v-model="captcha" class="message" placeholder="输入验证码">
          <img :src="captchasrc" class="code" @click="RefreshSafety" alt="">
        </div>
        <!--记住密码-->
        <div class="save-password" v-show="loginType===0">
          <i class="iconfont " :class="savePassword ? 'digicon-roundcheck' : 'digicon-round'" @click="isRemember"></i>
          <span class="re" @click="isRemember">记住密码</span>
        </div>
        <!--账号密码登录报错-->
        <div class="error-code" v-show="login_pass_error && loginType===0">
          <i class="iconfont digicon-warn_light"></i>
          <span>{{login_pass_error}}</span>
        </div>
        <!--手机号登录报错-->
        <div class="error-code" v-show="login_phone_error && loginType===1">
          <i class="iconfont digicon-warn_light"></i>
          <span>{{login_phone_error}}</span>
        </div>
        <div class="login-btn" @click="login">立即登录</div>
        <div class="register-sw">
          <a class="register" href="javascript:void(0)">新用户注册</a>
          <a class="sw" href="javascript:void(0)">试玩</a>
        </div>
      </div>
    </div>
    <router-link to="javascript:void(0)" class="forget-password">忘记密码</router-link>
  </div>
</template>

<script type='text/ecmascript-6'>
  export default {
    data() {
      return {
        login_pass_error: '', // 账号密码登录消息错误提示
        login_phone_error: '', // 手机号验证码登录错误提示
        username: '', // 用户名
        mobile: '', // 手机号码
        verifyImg: '',
        loginType: 0, // 登录方式 0为账号密码登录 1为手机登录
        countTime: 60, // 倒计时数字
        verifyCode: '', // 手机发送验证码
        isSendVerfyCode: false, // 是否已经发送了验证码
        pass: '', // 密码
        passType: true, // 密码显示类型
        captcha: '', // 验证码
        initcode: '____', // 初始的密码
        captchasrc: '', // 验证码图片地址
        savePassword: localStorage.getItem('_save_password') !== 'true' ? false : true, // 是否保存密码
        timer: null // 定时器
      }
    },
    methods: {
      init() {
        let skey = Object.keys(localStorage)
        // 如果localStorage没有数据则为第一次登录
        if (skey.length === 0) {
          this.savePassword = false
          localStorage.setItem('_save_password', 'false')
          return false
        }
        let lastLogin = localStorage.getItem('_last') === 'true'
        let pass = localStorage.getItem('_pass')
        if (lastLogin) {
          this.username = lastLogin
        }
        if (pass && lastLogin && this.savePassword) {
          this.pass = this.initcode
        }
      },
      // 是否保存密码
      isRemember() {
        if (this.savePassword === true) {
          this.savePassword = false
          localStorage.setItem('_save_password', 'false')
        } else {
          this.savePassword = true
          localStorage.setItem('_save_password', 'true')
        }
      },
      // 获取图片验证码
      RefreshSafety() {
        // this.captchasrc = '/yx/getImgCode' + '?t=' + new Date();
      },
      // 改变登录方式
      switchType() {
        this.loginType = this.loginType === 0 ? 1 : 0
        if (this.loginType === 1 && !this.captchasrc) {
          this.RefreshSafety()
        }
      },
      // 获取短信验证码
      getVerifyCode: function () {
        if (this.isSendVerfyCode) {
          this.login_phone_error = '已经发送验证码，请注意查看'
          return false
        }
        this.isSendVerfyCode = true
        this.timer = setInterval(() => {
          if (this.countTime > 1) {
            this.countTime--
          } else {
            this.countTime = 60
            this.isSendVerfyCode = false
            clearInterval(this.timer)
          }
        }, 1000)
      },
      login: function () {
        this.$router.push({path: '/home'})
      }
    },
    created: function () {
      this.init()
    },
    watch: {},
    beforeDestroy() {
      clearInterval(this.timer)
    }
  }
</script>

<style lang='stylus'>
  @import "~@/common/stylus/mixins.styl"
  @import "./login.styl"
</style>



