<template>
  <div class="load-more-wrapper">
    <div class="cart-box" v-infinite-scroll="loadMore"
         infinite-scroll-disabled="loading"
         infinite-scroll-distance="10">
      <router-link to="" class="item border-1px" v-for="(item,index) in list" :key="index">
        <div class="word">
          <h4 class="title">{{item.title}}</h4>
          <div class="abstract">{{item.abstract}}</div>
          <div class="item_info">
            <span class="cmt">观看{{item.view}}</span>
          </div>
        </div>
        <div class="image">
          <img v-lazy="item.src" alt="">
        </div>
      </router-link>
    </div>
    <v-loading :loading="loading" :allLoaded="data.allLoaded"/>
  </div>
</template>

<script type='text/ecmascript-6'>
  import loading from '@/components/loading/loading'

  export default {
    props: ['data', 'activeIndex'], // activeIndex:当前栏目 data:{url: '', index: 0, loading: false, allLoaded: false}
    data() {
      return {
        list: [], // 数据列表
        page: 0
      }
    },
    computed: {
      loading() {
        return this.data.loading
      }
    },
    components: {
      'v-loading': loading
    },
    methods: {
      loadMore() {
        if (this.data.allLoaded) {
          return false
        }
        console.log(this.data.index, '------loadingItem----', this.page)
        this.data.loading = true
        let data = [{
          title: '求这个图原图的地址_百度知道',
          abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
          view: 10 + Math.ceil(Math.random() * 10000),
          src: require('../../home/activity_item_1.jpg')
        }, {
          title: '求这个图原图的地址_百度知道',
          abstract: '求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道求这个图原图的地址_百度知道',
          view: 10 + Math.ceil(Math.random() * 10000),
          src: require('../../home/activity_item_1.jpg')
        }]
        setTimeout(() => {
          this.page++
          this.list.push(...data)
          if (this.page === 3) {
            this.data.allLoaded = true
            return false
          }
          // 当接口还未返回数据就已经切换了栏目阿 添加完数据后直接return
          if (this.data.index !== this.activeIndex) {
            this.data.loading = true
            return false
          }
          this.data.loading = false
        }, 3000)
      }
    },
    created() {
    },
    mounted() {
    },
    watch: {
      activeIndex(val) {
        console.log(val, '----activeIndex')
        // 当切换了栏目的时候就要停止加载
        if (this.data.index !== val) {
          this.data.loading = true
        } else {
          // 再次切换回来 首先判断是否已经加载完毕
          if (!this.data.allLoaded) {
            this.data.loading = false
          } else {
            this.data.loading = true
          }
        }
      }
    }
  }
</script>

<style lang='stylus'>
  @import "~@/common/stylus/mixins.styl"
  .load-more-wrapper
    font-size: 0
    line-height: 1
    text-align: left
    .cart-box
      background-color: #fff
      .item
        display: block
        border-1px(rgba(221, 221, 221, 0.6))
        padding: 0.24rem 0
        margin: 0 0.24rem
        .image
          display: inline-block
          vertical-align: middle
          width: 33%
          position: relative
          overflow: hidden
          padding-percent(60%)
          img
            position: absolute
            top: 0
            left: 0
            width: 100%
            height: 100%
            border-radius: 0.08rem
        .word
          display: inline-block
          vertical-align: middle
          width: 65%
          margin-right: 2%
          .title
            font-size: 14px
            ellipse()
            color: #222
          .abstract
            margin-top: 0.12rem
            font-size: 12px
            line-height: 1.3
            max-line(3)
            color: #666
            min-height: 30px
          .item_info
            .cmt
              display: inline-block
              vertical-align: top
              font-size: 10px
              padding-top: 0.06rem
              color: #aaa
</style>



